@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  color: rgb(237, 237, 237);
  background-color: #009dffec;
}

.container {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid rgb(0, 0, 0);
  padding: 30px;
  border-radius: 5px;
  background-color: rgb(42, 42, 42);
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
  
  background: rgba(0, 0, 0, 0.499);
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 4px );
  -webkit-backdrop-filter: blur( 4px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  background: rgba(0, 0, 0, 0.45);
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.task {
  background: #1515158c;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
  background: rgba(0, 0, 0, 0.45);
  box-shadow: 0 8px 32px 0 rgba(0, 157, 255, 0.038);
  backdrop-filter: blur( 0px );
  -webkit-backdrop-filter: blur( 0px );
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, 0.159);
}

.task.reminder {
  border-left: 5px solid #009dff;
}


.task.done {
  background: rgba(0, 0, 0, 0.112);
  color: rgba(255, 255, 255, 0.249);
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1 1;
}

.form-control-check input {
  flex: 2 1;
  height: 20px;
  margin-right: 40px;
  cursor: pointer;
}

.form-control-date {
  margin-left: 0px;
}

.date-picker {
  height: 40px;
  margin-left: 5px;
  background-color: rgb(255, 255, 255);
  color: rgb(0, 40, 64);
}

.date-picker input {
  padding: 0px 8px;
  margin: 0px;
  color: rgb(0, 40, 64);
}

footer {
  margin-top: 120px;
  text-align: center;
}

.about-link {
  color:rgb(255, 255, 255);
  text-decoration: none;
  border: solid 1px;
  border-radius: 5px;
  padding: 4px 8px;
  font-size: 14px;
}

.about-link:hover {
  color: #009dff;
}

.about-link-hidden {
  display: none;
}

.about-page p {
  margin-top: 10px;
  margin-left: 5px;
}

.back-btn-container {
  margin-top: 15px;
}

.go-back-btn {
  color: rgb(161, 161, 161);
  text-decoration: none;
}

.go-back-btn:hover {
  color: rgb(0, 157, 255);
}

.markAsDone {
  display: flex;
  justify-content: flex-end;
  font-size: 12px;
}

.markAsDone label {
  margin-right: 8px;
}

.markAsDone input {
  margin-right: 2px;
  cursor: pointer;

}

.reminder-modal {
  position: absolute;
  height: 200px;
  width: 280px;
  left: 110px;
  top: 80px;
  margin: 0 auto;
  padding: 10px;
  cursor: default;
  background: rgba(0, 0, 0, 0.45);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(9px);
  -webkit-backdrop-filter: blur(9px);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 6px;

}

.reminder-modal h4 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.test {
  position: relative;
  height: 35px;
  width: 200px;
  margin-top: 5px;
}

.test input {
  border-radius: 4px;
  background: rgb(255, 255, 255);
}

.reminder-modal input {
  margin-top: 10px;
  margin-right: 10px;
  height: 28px;
  width: 40px;
  font-size: 14px;
  text-align: center;
}

.set-reminder-btn-container {
  text-align: center;
  margin-top: 35px;
}

.set-reminder-btn {
  font-size: 16px;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  background-color: #009dffec;
  color: white;
  cursor: pointer;
}

.set-reminder-btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}




.modal-overlay {
  position: fixed;
  background-color: rgba(42, 42, 42, 0.573);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
